<utd-accordeon reduit="false">
    <div slot="titre"><h2>1.8.0 (2023-02-??)</h2></div>
    <h3>Amliorations</h3>
    <ul class="utd">
        <li>Ajout du composant <a href="/composants/formulaire/listederoulante">Liste droulante</a>.</li>
        <li>Tailles adaptatives pour les headers (h1  h6).</li>
    </ul>
</utd-accordeon>

<utd-accordeon reduit="true">
    <div slot="titre"><h2>1.7.6 (2023-01-18)</h2></div>
    <h3>Amliorations</h3>
    <ul class="utd">
        <li>Composant <a href="/composants/affichagecontenu/infobulle">Infobulle</a>
            <ul>
                <li>Ajustement du composant selon les nouvelles normes du systme de design Quebec.ca (padding + largeur ascenceur).</li>
                <li>Ajout du paramtre "sr-prefixe-contenu". Ce texte permet d'amliorer l'accessibilit du composant. Il s'agit d'un texte hors cran qui reoit le focus lorsque l'infobulle est affiche. Il permet de ne pas lire tout le contenu de l'infobulle lors de son ouverture. Il est positionn juste avant la zone de contenu de l'infobulle.</li>
            </ul>
        </li>

        <li>Restructuration du projet
            <ul>
                <li>Dbut du passage des css vers sass.</li>
                <li>Restructuration de certains rpertoires de la solution.</li>    
            </ul>
        </li>
    </ul>
</utd-accordeon>

<utd-accordeon reduit="true">
    <div slot="titre"><h2>1.7.5 (2023-01-10)</h2></div>
    <h3>Amliorations</h3>
    <ul class="utd">
        <li>Accessibilit du composant <a href="/composants/affichagecontenu/infobulle">Infobulle</a>. Le titre tait lu 3 fois (au lieu de 2 le mieux que nous pouvons faire) et le bouton "Fermer" tait aussi lu automatiquement (au lieu de le lire seulement lorsqu'il reoit le focus).</li>
    </ul>
</utd-accordeon>

<utd-accordeon reduit="true">
    <div slot="titre"><h2>1.7.4 (2022-12-16)</h2></div>
    <h3>Amliorations</h3>
    <ul class="utd">
        <li>Mise  jour du site de documentation.</li>
        <li>Ajustement des largeurs par dfaut des champs de types (email, password, number, url et date).</li>
    </ul>    
</utd-accordeon>

<utd-accordeon reduit="true">
    <div slot="titre"><h2>1.7.3 (2022-12-15)</h2></div>
    <h3>Correction d'anomalies</h3>
    <ul class="utd">
        <li>Ajout d'un cache buster sur l'url du css import dans les diffrents composants.</li>
    </ul>    
</utd-accordeon>

<utd-accordeon reduit="true">
    <div slot="titre"><h2>1.7.2 (2022-12-08)</h2></div>
    <h3>Amliorations</h3>
    <ul class="utd">
        <li>Ajout du composant <a href="/composants/piv/piventete">Piv entte</a></li>
        <li>Ajout du composant <a href="/composants/piv/pivpiedpage">Piv pied page</a></li>
        <li>Ajout du composant <a href="/composants/navigation/piedpagesite">Pied de page de site</a></li>
        <li>Ajout de 2 gabarits contenant la structure HTML de base pour l'intgration des composants UTD. <a href="/base/principe">Voir la section "Base"</a></li>
        <li>Ajout de la possibilit de spcifier le chemin relatif du dossier contenant les fichiers utd via l'attribut "relative-base-path" de la balise script.</li>
    </ul>    
</utd-accordeon>

<utd-accordeon>
    <div slot="titre"><h2>1.7.1 (2022-11-28)</h2></div>
    <h3>Amliorations</h3>
    <ul class="utd">
        <li>Composant <a href="/composants/actions/dialogue">Dialogue</a>, si aucun contrle de saisie prsent dans la fentre de dialogue et que le paramtre "id-focus-ouverture" n'est pas spcifi, le focus est donn automatiquement au bouton primaire (comme pour un message).</li>
    </ul>
    
    <h3>Corrections d'anomalies</h3>
    <ul class="utd">
        <li>
            <span>Notification</span>
            <ul>
                <li>Corrections accessibilit. Ajout espace forc au lecteur cran entre titre et contenu. Le bouton de fermeture ne fait plus partie de l'alerte qui est lu au lecteur cran.</li>            
            </ul>
        </li>
        <li>
            <span>Dploiement</span>
            <ul>
                <li>Retrait d'un rpertoire "node_modules" qui tait inclu dans le code source de la solution.</li>            
            </ul>
        </li>
    </ul>    
</utd-accordeon>

<utd-accordeon>
    <div slot="titre"><h2>1.7.0 (2022-11-24)</h2></div>
    <h3>Amliorations</h3>
    <ul class="utd">
        <li>Bonification importante de la documentation (attributs et slots)</li>
        <li>Ajout du composant <a href="/composants/navigation/menuvertical">Menu vertical</a></li>
        <li>Ajout du composant <a href="/composants/navigation/menuancres">Menu d'ancres</a></li>
        <li>Ajout du composant <a href="/composants/affichagecontenu/section">Sections</a></li>
        <li>Composant <a href="/composants/affichagecontenu/section">Avis</a> mise  jour des couleurs et maintenant focusable</li>
        <li>
            <span>Champs de formulaires</span>
            <ul>
                <li>Ajout des styles pour les champs de formulaire.
                </li>            
                <li>Ajout du composant "utd-champ-form" qui permet d'encapsuler les champs de formulaires afin de simplifier grandement leur utilisation. Il prend notamment en charge la gestion du label, de la prcision, du champ requis, du message d'erreur et videmment de tous les attributs requis pour l'accessibilit.
                </li>            
            </ul>
        </li>
    </ul>
    
    <h3>Corrections d'anomalies</h3>
    <ul class="utd">
        <li>
            <span>Accordon</span>
            <ul>
                <li>Correction de l'animation de fermeture. Il y avait un dcalage du contenu pendant qu'il disparassait.</li>            
            </ul>
        </li>
    </ul>
    
    <h3>Breaking changes</h3>
    <ul class="utd">
        <li>
            <span>Infobulle</span>
            <ul>
                <li>Attribut renomm. srtitre devient sr-titre.</li>            
                <li>Attribut renomm. srboutonfermer devient sr-bouton-fermer.</li>            
                <li>Attribut renomm. srboutonouvrir devient sr-bouton-ouvrir.</li>            
            </ul>
        </li>
        <li>
            <span>Dialogue modal</span>
            <ul>
                <li>Attribut renomm. srboutonfermer devient sr-bouton-fermer.</li>            
                <li>Attribut renomm. idfocusouverture devient id-focus-ouverture.</li>            
                <li>Attribut renomm. idfocus devient id-focus-fermerture.</li>            
                <li>Attribut renomm. estboutonstextelong devient boutons-textelong.</li>            
                <li>Attribut renomm. estaffichageboutonsinline devient forcer-boutons-inline.</li>            
                <li>Attribut renomm. estaffichagelateral devient affichage-lateral.</li>            
            </ul>
        </li>
    </ul>        
</utd-accordeon>

<utd-accordeon>
    <div slot="titre"><h2>1.6.1 (2022-09-18)</h2></div>
    <h3>Corrections d'anomalies</h3>
    <ul class="utd">
        <li>
            <span>Accordon</span>
            <ul>
                <li>Correction alignement texte du titre lorsqu'il est affich sur plusieurs lignes. Ajustements mineurs avec nouvelles spcifications Quebec.ca.
                </li>            
            </ul>
        </li>
        <li>
            <span>Traitement en cours</span>
            <ul>
                <li>La mthode "debuter" ne dsactivait pas le bouton cliqu si "avecOverlay" tait "true". L'overlay  lui seul bloquait les clics subsquents sur le bouton pendant le traitement, mais n'empchait pas la touche "ENTER".
                </li>            
            </ul>
        </li>
    
    </ul>        
</utd-accordeon>

<utd-accordeon>
    <div slot="titre"><h2>1.6.0 (2022-07-19)</h2></div>
    <h3>Amliorations</h3>
    <ul class="utd">
        <li>
            <span>Dialogue modale</span>
            <ul>
                <li>Ajout du paramtre "estaffichageboutonsinline" (optionnel) permettant de forcer l'affichage sur une seule ligne des boutons de la slot "pied".</li>
                <li>Ajout du paramtre "estaffichagelateral" (optionnel) permettant que le dialogue modale s'affiche de droite  gauche (et non simplement avec un fade comme par dfaut).</li>
                <li>Ajout du paramtre "idfocusouverture" (optionnel) permettant de spcifier un id de contrle auquel donner le focus  l'affichage.</li>               
            </ul>
        </li>    
    </ul>
    
    <h3>Corrections d'anomalies</h3>
    <ul class="utd">
        <li>
            <span>Base</span>
            <ul>
                <li>Ajustements afin de retirer tous les conflits de css potentiels avec les diffrents projets. Ex. Retrait de marges suprieures aux diffrents headers par dfaut.</li>            
                <li>Ajustement mineur css bouton compact (hauteur minimale).</li>            
                <li>Ajout css pour affichage des erreurs de champs (classe utd-erreur-champ).</li>            
            </ul>
        </li>
        <li>
            <span>Notifications</span>
            <ul>
                <li>Positionnement revu lorsque le composant "Retour haut page" est affich. Comme la notification est temporaire, on l'affiche par dessus le bouton car sinon la notification est beaucoup top haute et a ne fait pas de sens (mme si contradictoire avec ce qui est dfini dans le systme de design Quebec.ca).</li>            
                <li>Ajout du paramtre "estaffichageboutonsinline" permettant de forcer l'affichage sur une seule ligne des boutons de la slot "pied".</li>            
            </ul>
        </li>
    </ul>        
</utd-accordeon>

<utd-accordeon>
