<script>
    import CodeSource from '../components/CodeSource.svelte'; 
    import { onMount } from 'svelte';
    import TableauParams from '../components/TableauParams.svelte'; 
    import TableauSlots from '../components/TableauSlots.svelte'; 

    let tableauParametres = [];
    let tableauSlots = [];

    onMount(() => {
        tableauParametres = obtenirTableauParametres();
        tableauSlots = obtenirTableauSlots();
    })


    function obtenirTableauParametres() {
        return [
            {nom: "reduit", type: "Boolean (Optionnel)", description: `Indique si l'accordon doit tre rduit ou dvelopp. Dfaut "true" (rduit).`},    
            {nom: "titre", type: "String (Optionnel)", description: `Titre affich dans l'entte de l'accordon.`},
            {nom: "tag-titre", type: "String (Optionnel)", description: `Balise html  utiliser pour le titre de l'entte. Dfaut "h2".`},
            {nom: "contenu", type: "String (Optionnel)", description: `Texte  afficher dans la zone de contenu de l'accordon.`}

        ];
    }

    function obtenirTableauSlots() {
        return [
            {nom: "dfaut", description: `<p>Slot par dfaut. Aucun nom  fournir.</p><p>Contenu html entre les balises du contrle. Est inject dans la zone de contenu de l'accordon.</p>`},
            {nom: "titre", description: `Contenu html inject dans l'entte de l'accordon. Exemple d'utilisation : Ajout d'une icne  gauche du titre.`}            
        ];
    }

</script>

<style type="text/css">
</style>

<h1>Accordon</h1>

<utd-menu-ancres selecteur="#main h2">
</utd-menu-ancres>

<h2>Description</h2>
<p>L<strong>accordon</strong> permet de rduire temporairement la quantit dinformation affiche sur une page. Avec cette option, la personne survole le contenu et accde plus rapidement  linformation recherche.</p>

<h3>Rfrence systme de design Quebec.ca</h3>
<a href="https://design.quebec.ca/composantes/affichage-de-contenu/accordeon" target="_blank">Voir les spcifications sur le site de design Quebec.ca</a>

<h2>Attributs disponibles</h2>
<TableauParams parametres="{tableauParametres}">
</TableauParams>

<h2>Slots disponibles</h2>
<TableauSlots parametres="{tableauSlots}">
</TableauSlots>


<h2>Exemples</h2>
<h3>1- Avec attributs "titre" et "contenu"</h3>
<div class="mb-32" id="exempleAccordeon1">
    <utd-accordeon titre="Exemple d'utilisation 1" contenu="<p>Ici un texte incroyable.</p><p>Qui peut mme contenir du html.</p>">
    </utd-accordeon>
</div>
<CodeSource idElementCodeSource="exempleAccordeon1">
</CodeSource>   


<h3>2- Avec attribut "titre", slot par dfaut et tag-titre h5</h3>
<div class="mb-32" id="exempleAccordeon2">
    <utd-accordeon titre="Exemple d'utilisation 2" tag-titre="h5">
        <p>Dans cet exemple, le niveau de titre de l'accordon a t forc  "h5" au lieu de la valeur par dfaut "h2" via l'attribut <span class="utd-emphase-gris">tag-titre</span>.</p>
    </utd-accordeon>
</div>
<CodeSource idElementCodeSource="exempleAccordeon2">
</CodeSource>   


<h3>3- Avec slots "titre" et slot par dfaut</h3>
<div class="mb-32" id="exempleAccordeon3">
    <utd-accordeon class="mb-32">
        <span slot="titre">
            Exemple d'utilisation 3 avec un titre long long long long long long long long long long long long long long long long long long
        </span>   
        <p>Ici un texte incroyable.</p>
        <p>Qui peut mme contenir du html.</p>
    </utd-accordeon>
</div>
<CodeSource idElementCodeSource="exempleAccordeon3">
</CodeSource>   


